<template>
  <div class="controller">
    <section class="login" v-if="!term">
      <div class="form-group">
        <label for="inputhost">Host</label>
        <input type="text" class="form-control" v-model.trim="ip" />
        <small id="emailHelp" class="form-text text-muted">服务器ip地址.</small>
      </div>
      <div class="form-group">
        <label for="InputEmail1">用户名</label>
        <input
          type="text"
          class="form-control"
          aria-describedby="emailHelp"
          v-model.trim="username"
        />
        <small id="emailHelp" class="form-text text-muted">不会记录你的登录信息, 可通过SSH连接至你的服务器.</small>
      </div>
      <div class="form-group">
        <label for="InputPassword">密码</label>
        <input
          type="password"
          class="form-control"
          v-model.trim="password"
          id="exampleInputPassword1"
        />
      </div>
      <div class="form-group form-check">
        <input type="checkbox" class="form-check-input" id="exampleCheck1" />
        <label class="form-check-label" for="exampleCheck1">Check me out</label>
      </div>
      <button @click.prevent="submit" class="btn btn-primary">提交</button>
    </section>
    <!-- <input type="text" v-model="username" placeholder="用户名" />
        <input type="password" v-model="password" placeholder="密码" />
    <button >开始链接</button>-->
    <div class="console" id="terminal"></div>
  </div>
</template>

<script>
import "xterm/css/xterm.css";
import { Terminal } from "xterm";
import { FitAddon } from "xterm-addon-fit";
export default {
  name: "Xterm",
  props: {},
  data() {
    return {
      term: null,
      password: "cBwr0FPRASHJ",
      username: "tourist",
      ip: "106.13.184.92"
    };
  },
  activated() {},
  deactivated() {},
  methods: {
    initTerm() {
      const term = new Terminal({
        // rows: this.rows, //行数
        // cols: 10, // 设置之后会输入多行之后覆盖现象
        // scrollback: 10,//终端中的回滚量
        // scrollback: 30,
        rendererType: "canvas", //渲染类型
        convertEol: true, //启用时，光标将设置为下一行的开头
        fontSize: 14, //字体大小
        disableStdin: false, //是否应禁用输入。
        cursorStyle: "block", //光标样式
        cursorBlink: true, //光标闪烁
        tabStopWidth: 4,
        theme: {
          foreground: "white", //字体
          background: "#1d4f5c" //背景色
        }
      });
      const fitAddon = new FitAddon();
      term.loadAddon(fitAddon);
      term.open(document.getElementById("terminal"));
      term.focus();
      term.write(
        "\rAcross the Great Wall we can reach every corner in the world.\n$ "
      );

      this.term = term;
    },
    submit() {
      if (!this.$socket.id) {
        let timeOut = setTimeout(() => {
          this.submit();
          clearTimeout(timeOut);
        }, 500);
        return;
      }
      if (!this.term) {
        this.$socket.emit("createNewSSH", {
          id: this.$socket.id,
          ip: this.ip,
          username: this.username,
          password: this.password
        });
        this.addListen();
      } else {
        this.exit();
        this.addListen();
      }
    },
    addListen() {
      const Form = {};
      let outTime = 3;
      let waitTime = 12;
      let waitTimer = null;
      let timer = null;
      // 初始化
      this.initTerm();
      // 输入提交
      this.term.onData(key => {
        this.$socket.emit(this.$socket.id + "_ssh", key);
      });
      // 等待事件
      waitTimer = setInterval(() => {
        try {
          this.term.write(`\n正在尝试连接! ${waitTime}秒`);
          waitTime--;
          if (waitTime <= 0) {
            clearInterval(waitTimer);
            this.term.clear();
            this.term.write(`\n连接失败!`);
          }
        } catch (error) {
          clearInterval(waitTimer);
          this.exit();
        }
      }, 1000);
      // 监听接收
      this.$socket.on(this.$socket.id + "_ssh", data => {
        if (waitTimer) {
          clearInterval(waitTimer);
          waitTimer = null;
        }
        // 接收写入
        this.term.write(data);
        // 监听退出
        if (data.trim() == this.$socket.id + "_ssh_logout") {
          timer = setInterval(() => {
            try {
              this.term.write(`\n${outTime} 秒后自动退出!`);
              outTime--;
              if (outTime <= 0) {
                clearInterval(timer);
                timer = null;
                this.exit();
              }
            } catch (error) {
              clearInterval(timer);
              this.exit();
            }
          }, 1000);
        }
      });
    },
    // 退出移除所有监听
    exit() {
      let terminal = document.querySelector("#terminal");
      let xterm = document.querySelector(".xterm");
      if (xterm) {
        terminal.removeChild(xterm);
      }
      this.$socket.emit("rmListener", {
        key: this.$socket.id + "_ssh"
      });
      this.$socket.removeListener(this.$socket.id + "_ssh");
      this.term = null;
    }
  }
};
</script>


<style lang="scss"  scoped>
.controller {
  width: 100%;
}
.login {
  box-sizing: border-box;
  padding: 1rem;
  max-width: 375px;
}
#terminal {
  width: 100%;
}
</style>